<template>
    <div class="image-loader-container">
        <img class="placeholder" v-if="!loaded" :src="placeholder" >
        <img class="origin"
             :src="src"
             @load="handleLoad"
             :style="{ opacity: displayOpacity, transition: `${duration}ms` }"
        >
        
    </div>
</template>

<script>
export default {
    props : {
        src : {
            type : String,
            required : true,
        },
        placeholder : {
            type : String,
            required : true,
        },
        duration : {
            type : Number,
            default : 2000,
        }
    },
    computed : {
        displayOpacity(){
            return this.isLoad ? 1 : 0;
        }
    },
    data(){
        return{
            isLoad : false,
            loaded : false
        }
    },
    methods : {
        handleLoad(){
            this.isLoad = true;
            setTimeout(()=>{
                this.loaded = true;
                this.$emit('load');
            },this.duration)
        }
    }
}
</script>

<style lang="less" scoped>
    @import "../../styles/mixin.less";
    .image-loader-container{
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;
        img{
            .self-fill();
            object-fit: cover;
        }
    }
    .placeholder{
        filter: blur(2vw);
    }
</style>