<template>
<!-- 只有在总页数大于一时显示 -->
  <div class="pager-container" v-if="totalPage > 1">
      <a @click = "handleClick(1)" :class="{disabled : current === 1}">|&lt;&lt;</a>
      <a @click = "handleClick(current - 1)"  :class="{disabled : current === 1}">&lt;&lt;</a>
      <a @click = "handleClick(index)"  v-for="(index, id) in getEnableNum" :key="id" :class="{active : index == current}">{{ index }}</a>
      <a @click = "handleClick(current + 1)"  :class="{disabled : current === total}">&gt;&gt;</a>
      <a @click = "handleClick(totalPage)"  :class="{disabled : current === total}">&gt;&gt;|</a>
  </div>
</template>

<style lang="less" scoped>
    @import "~@/styles/var.less";
    .pager-container{
        display: flex;
        justify-content: center;
        margin: 20px 0;
        a {
            color: @primary;
            margin: 0 6px;
            cursor: pointer;
            &.disabled{
                color: @lightWords;
                cursor: not-allowed;
            }
            &.active{
                color: @words;
                font-weight: bold;
                cursor: text;
            }
        }
    }
</style>


<script>
export default {
    props : {
        current : { //当前页码
            type : Number,
            required : false,
            default : 1,
        },
        total : { //总数据量
            type : Number,
            required : false,
            default : 0,
        },
        limit : { //页容量
            type : Number,
            required : false,
            default : 10,
        },
        visibleNumber : { //可见页码数
            type : Number,
            required : false,
            default : 10,
        }
    },
    methods : {
        handleClick(newPage){
            if(newPage < 1){
                newPage = 1;
            }
            if(newPage > this.total){
                newPage = this.total;
            }
            if(newPage == this.current){
                return ;
            }
            this.$emit('pageChange', newPage);
        }
    },
    computed : {
        totalPage(){ //获取总页数
            return Math.ceil(this.total / this.limit);
        },
        getMinNum(){ //获取选择条最小数字
            let min = this.current - Math.floor(this.visibleNumber / 2);
            if(min < 1) {
                min = 1;
            }
            return min;
        },
        getMaxNum(){ //获取选择条最大数字
            let max = this.getMinNum + this.visibleNumber - 1;
            if( max > this.totalPage ){
                max = this.totalPage;
            }
            return max;
        },
        getEnableNum(){ //获取可以选择的页码条
            let enableNum = [];
            for(let i = this.getMinNum; i <= this.getMaxNum; i++){
                enableNum.push(i);
            }
            return enableNum;
        }
    }
}
</script>

