<template>
  <div class="blog-list-container" ref="mainContainer" v-loading="isLoading">
    <ul>
      <li v-for="item in data.rows" :key="item.id">
        <div class="thumb">
          <router-link :to="{name : 'BlogDetail', params : {
            id : item.id,
          }}">
            <img
              v-lazy="item.thumb"
              :alt="item.title"
              :title="item.title"
            />
          </router-link>
        </div>
        <div class="main">
          <router-link :to="{name : 'BlogDetail', params : {
            id : item.id,
          }}">
            <h2>
                {{ item.title }}
            </h2>
          </router-link>
          <div class="aside">
            <span>日期：{{ formatTime(item.createDate) }}</span>
            <span>浏览：{{ item.scanNumber}}</span>
            <span>评论{{ item.commentNumber }}</span>
            <router-link :to="{
                name: 'CategoryBlog',
                params: {
                  categoryId: item.category.id,
                },
              }" >{{ item.category.name}}</router-link>
          </div>
          <div class="desc">
            {{ item.description }}
          </div>
        </div>
      </li>
    </ul>
    <Page v-if="data.total"
          :current="routeInfo.page"
          :total="data.total"
          @pageChange="handlePageChange"
          :limit="routeInfo.limit"/>
  </div>
</template>

<script>
import Page from "@/components/Pager";
import fetchData from "@/mixins/fetchData";
import {getBlogs} from "@/api/blog.js";
import formatTime from "@/utils/formatTime.js";
export default {
    mixins: [fetchData({})],
    components: {
        Page,
    },
    mounted(){
      this.$bus.$on("setMainScroll",this.handleSetMainScroll);
      this.$refs.mainContainer.addEventListener("scroll",this.handleScroll);
    },
    computed: {
      routeInfo(){
        const categoryId = +this.$route.params.categoryId || -1;
        const page = +this.$route.query.page || 1;
        const limit = +this.$route.query.limit || 10;
        return {
          categoryId,
          page,
          limit
        }
      }
    },
    watch : {
      async $route(){
        this.isLoading = true;
        // 点击分页跳转时，将滚动条重置
        this.$refs.mainContainer.scrollTop = 0;
        this.data = await this.fetchData();
        this.isLoading = false;
      }
    },
    beforeDestroy(){
      this.$bus.$emit("mainScroll");
      this.$bus.$off("setMainScroll",this.handleSetMainScroll);
      this.$refs.mainContainer.removeEventListener("scroll",this.handleScroll);
    },
    methods: {
        async fetchData(){
            return await getBlogs(this.routeInfo.page,this.routeInfo.limit,this.routeInfo.categoryId);
        },
        handleScroll(){
          this.$bus.$emit("mainScroll",this.$refs.mainContainer);
        },
        handleSetMainScroll(pos){
          this.$refs.mainContainer.scrollTop = pos;
        },
        formatTime,
        handlePageChange(newPage){
          const query = {
            page: newPage,
            limit : this.routeInfo.limit
          }
          //通过改变路由，来控制页面跳转。
          // this.$router.push() 进行无刷新跳转
          if(this.routeInfo.categoryId == -1){
            this.$router.push({
              name : "Blog",
              query,
            });
          }else{
            this.$router.push({
              name : "CategoryBlog",
              query,
              params : {
                categoryId : this.routeInfo.categoryId
              }
            });
          }
        }
    }
}
</script>

