<template>
  <div class="blog-toc-container">
    <h2>目录</h2>
    <RightList :list="tocWithSelect" @select="handleSelect" />
  </div>
</template>

<script>
import RightList from "./RightList";
import {debounce} from "@/utils";
export default {
  components: {
    RightList,
  },
  props: {
    toc: {
      type: Array,
    },
  },
  data(){
    return {
      activeAnchor : "",
    }
  },
  computed : {
    //根据rightList传过来的toc数组，和被激活的锚点，组成一个新的带有select：true & false的数组
    tocWithSelect(){
      const getTOC = (toc = []) => {
        return toc.map(item => {
          return {
            ...item,
            isSelect : item.anchor == this.activeAnchor,
            children : getTOC(item.children)
          }
        })
      }
      return getTOC(this.toc);
    },
    //根据toc数组得到平铺的对应dom数组
    doms(){
      const doms = [];
      const addDom = toc => {
        for (const item of toc) {
          let dom = document.getElementById(item.anchor);
          doms.push(dom);
          if(item.children && item.children.length){
            addDom(item.children);
          }
        }
      }
      addDom(this.toc);
      return doms;
    }
  },
  created(){
    this.setSelectDebounce = debounce(this.setSelect, 50);
    this.$bus.$on("mainScroll",this.setSelectDebounce);
  },
  destroyed(){
    this.$bus.$off("mainScroll",this.setSelectDebounce);
  },
  methods: {
    handleSelect(item) {
      location.hash = item.anchor;
    },
    setSelect(scrollDom){
      if(!scrollDom) return;
      this.activeAnchor = "";
      const range = 200;
      for(const dom of this.doms){
        if(!dom){
          continue;
        }
        const top = dom.getBoundingClientRect().top;
        if(top >= 0 && top <= range){
          //在规定范围内
          this.activeAnchor = dom.id;
          return;
        }else if(top > range){
          //在规定范围下方
          return;
        }else{
          //在规定范围上方
          this.activeAnchor = dom.id;
        }
      }
    }
  },
};
</script>

<style scoped lang="less">
.blog-toc-container {
  h2 {
    font-weight: bold;
    letter-spacing: 2px;
    font-size: 1em;
    margin: 0;
  }
}
</style>
