<template>
  <div class="home-container"
       @wheel="handleWheel"
       v-loading="loading">
      <ul class="carousel-container" 
          :style="{
            marginTop,
          }"
          @transitionend="isSwitch = false">
        <li v-for="item in data" :key="item.id">
          <CarouselItem :src="item.bigImg" 
                        :placeholder="item.midImg"
                        :title="item.title"
                        :description="item.description"/>
        </li>
      </ul>
      <div class="icon arrow-up"
           v-show="index"
           @click="switchTo(index - 1)">
        <Icon type="arrowUp"/>
      </div>
      <div class="icon arrow-down"
           v-show="index != data.length - 1"
           @click="switchTo(index + 1)">
        <Icon type="arrowDown"/>
      </div>
      <ul class="indicator">
        <li :class="{active : index == i}" 
            v-for="(item, i) in data" 
            :key="item.id"
            @click="index = i"></li>
      </ul>
  </div>
  
</template>

<script>
import { mapState } from "vuex";
import CarouselItem from "./Carouselitem";
import Icon from "@/components/Icon";

export default {
    components : {
      CarouselItem,
      Icon,
    },
    data(){
      return{
        index : 0, //当前显示的第几张轮播
        isSwitch : false,
      }
    },
    methods : {
      switchTo(i){
        this.index = i;
      },
      handleWheel(e){
        if(e.deltaY > 70 && !this.isSwitch){
          this.isSwitch = true;
          if(this.index == this.data.length - 1){
            this.isSwitch = false;
            return;
          }else{
            this.index ++ ;
          }
        }
        if(e.deltaY < -70 && !this.isSwitch){
          this.isSwitch = true;
          if(this.index == 0){
            this.isSwitch = false;
            return;
          }else{
            this.index -- ;
          }
        }
      }
    },
    computed : {
      marginTop(){
        return - document.documentElement.clientHeight * this.index + "px";
      },
      ...mapState("banner", ["loading", "data"])
    },
    created(){
      this.$store.dispatch("banner/fetchBanner")
    }
}
</script>

