@import "~@/styles/mixin.less";
@import "~@/styles/var.less";
  .home-container{
    overflow: hidden;// 设置的原因是因为外边距合并了
    width: 100%;
    height: 100%;
    position: relative;



    .carousel-container{
      width: 100%;
      height: 100%;
      transition: .4s;
      li{
        width: 100%;
        height: 100%;
      }
    }
    ul{
      margin: 0;
      list-style: none;
      padding: 0;
    }
    .icon{
      position: absolute;
      font-size: 30px;
      color: @gray;
      cursor: pointer;
      .self-center();
      transform: translateX(-50%);
      animation: jump 2s infinite;
      &.arrow-up{
        top: 25px;
      }
      &.arrow-down{
        top: auto;
        bottom: 25px;
      }
      @keyframes jump {
        0%{
          transform: translateY(0);
        }
        25%{
          transform: translateY(10px);
        }
        50%{
          transform: translateY(0);
        }
        75%{
          transform: translateY(-10px);
        }
        100%{
          transform: translateY(0px);
        }
      }
    }
    .indicator{
      .self-center();
      transform: translateY(-50%);
      left: auto;
      right: 30px;
      li{
        width: 7px;
        height: 7px;
        border-radius: 50%;
        background: @words;
        cursor: pointer;
        margin-bottom: 10px;
        border: 1px solid #fff;
        box-sizing: border-box;
        &.active{
          background: #fff;
        }
      }
    }
  }
</style>