<template>
  <div class="blog-comment-container">
    <MessageArea
      title="评论列表"
      :subTitle="`(${data.total})`"
      :list="data.rows"
      :isListLoading="isLoading"
      @submit="handleSubmit"
    />
  </div>
</template>

<script>
import MessageArea from "@/components/MessageArea";
import fetchData from "@/mixins/fetchData.js";
import { getComment, postComment } from "@/api/blog.js";
export default {
  mixins: [fetchData({ total: 0, rows: [] })],
  components: {
    MessageArea,
  },
  data() {
    return {
      page: 1,
      limit: 10,
    };
  },
  computed:{
    hasMore(){
      return this.data.rows.length < this.data.total;
    }
  },
  created(){
    this.$bus.$on("mainScroll",this.handleScroll);
  },
  beforeDestroy(){
    this.$bus.$off("mainScroll",this.handleScroll);
  },
  methods: {
    async fetchData() {
      return await getComment(this.page, this.limit, this.$route.params.id,);
    },
    //加载下一页
    async fetchMore(){
      if(!this.hasMore){
        return;
      }
      this.isLoading = true;
      this.page ++;
      const moreData = await this.fetchData();
      this.data.total = moreData.total;
      this.data.rows = this.data.rows.concat(moreData.rows);
      this.isLoading = false;
    },
    handleScroll(dom){
      if(this.isLoading || !dom) return;
      const range = 50;
      const dec = Math.abs((dom.scrollTop + dom.clientHeight) - dom.scrollHeight);
      if( dec < 50){
        this.fetchMore();
      }
    },
    async handleSubmit(formData, callback) {
      const resp = await postComment({
        blogId: this.$route.params.id,
        ...formData,
      });
      this.data.rows.unshift(resp);
      this.data.total++;
      callback("评论成功"); // 告诉子组件，我这边处理完了，你继续
    },
  },
};
</script>

<style scoped lang="less">
.blog-comment-container {
  margin: 30px 0;
}
</style>