<template>
  <div class="blog-category-container" 
       v-loading="isLoading"
       >
    <h2>文章分类</h2>
    <RightList :list="list"
                @select="handleSelect"/>
  </div>
</template>

<script>
import RightList from "./RightList";
import fetchData from "@/mixins/fetchData.js";
import {getBlogType} from "@/api/blog.js";
export default {
  mixins : [fetchData([])],
  components : {
    RightList,
  },
  computed : {
    categoryId(){
      return this.$route.params.categoryId || -1;
    },
    limit(){
      return +this.$route.query.limit || 10;
    },
    list(){
      const totalArticleCount = this.data.reduce((a, b)=> a + b.articleCount, 0);
      const result = [{name: "全部", id: -1,articleCount: totalArticleCount}, ...this.data];
      return result.map(item => ({
        ...item,
        isSelect : item.id == this.categoryId,
        aside : `${item.articleCount}篇`
      }))
    }
  },
  methods : {
    async fetchData(){
      return await getBlogType();
    },
    handleSelect(item){
      const query = {
        page: 1,
        limit: this.limit,
      }
      //通过改变路由，来控制页面跳转。
      // this.$router.push() 进行无刷新跳转
      if(item.id == -1){
        this.$router.push({
          name : "Blog",
          query,
        });
      }else{
        this.$router.push({
          name : "CategoryBlog",
          query,
          params : {
            categoryId : item.id
          }
        });
      }
    }
  }
}
</script>
 
<style scoped lang="less">
   .blog-category-container{
     width: 300px;
     box-sizing: border-box;
     padding: 20px;
     margin: 0;
     height: 100%;
     position: relative;
    h2{
      font-weight: bold;
      letter-spacing: 2px;
      font-size: 1em;
    }
   }
</style>