<template>
  <div class="carousel-item-container" 
       ref="container"
       @mousemove="handleMousemove"
       @mouseleave="handleMouseleave">
      <div class="carousel-img" 
           ref="image"
           :style="imgPosition">
          <ImageLoader :src="src"
                       :placeholder="placeholder"
                       @load="handleLoad"/>
      </div>
      <div class="title" ref="title">
          {{title}}
      </div>
      <div class="description" ref="desc">
          {{description}}
      </div>
  </div>
</template>

<script>
import ImageLoader from "@/components/ImageLoader";
export default {
    props : {
        src : {
            type : String,
            required : true,
        },
        placeholder : {
            type : String,
            required : true,
        },
        title : {
            type : String,
            required : true,
        },
        description : {
            type : String,
            required : false,
        }
    },
    data(){
        return {
            titleWidth : 0,
            descWidth : 0,
            containerSize : null, //容器大小
            innerSize : null, //图片大小
            mouseX : 0,//鼠标的横坐标
            mouseY : 0,//鼠标的纵坐标
        }
    },
    mounted(){
        this.titleWidth = this.$refs.title.clientWidth;
        this.descWidth = this.$refs.desc.clientWidth;
        this.setSize();
        this.mouseX = this.center.x;
        this.mouseY = this.center.y;
        window.addEventListener("resize",this.setSize);
    },
    destoryed(){
        window.removeEventListener("resize",this.setSize);
    },
    components : {
        ImageLoader,
    },
    methods : {
        showWords(){
            this.$refs.title.style.opacity = 1;
            this.$refs.title.style.width = 0;
            this.$refs.title.style.transition = "1s";
            this.$refs.title.clientWidth; //强行渲染
            this.$refs.title.style.width = this.titleWidth + "px";

            this.$refs.desc.style.opacity = 1;
            this.$refs.desc.style.width = 0;
            this.$refs.desc.style.transition = "2s 1s";
            this.$refs.desc.clientWidth; //强行渲染
            this.$refs.desc.style.width = this.descWidth + "px";
        },
        handleLoad(){
            this.showWords();
        },
        setSize(){
            this.containerSize = {
                width : this.$refs.container.clientWidth,
                height : this.$refs.container.clientHeight
            };
            this.innerSize = {
                width : this.$refs.image.clientWidth,
                height : this.$refs.image.clientHeight
            }
        },
        handleMousemove(e){
            const rect = this.$refs.container.getBoundingClientRect(); //获取dom元素的位置信息
            this.mouseX = e.clientX - rect.left;
            this.mouseY = e.clientY - rect.top;
            
        },
        handleMouseleave(){
            this.mouseX = this.center.x;
            this.mouseY = this.center.y;
        }
    },
    computed : {
        center(){
            return {
                x : this.containerSize.width / 2,
                y : this.containerSize.height / 2
            }
        },
        
        imgPosition(){
            if (!this.innerSize || !this.containerSize) {
                return;
            }
            const extraWidth = this.innerSize.width - this.containerSize.width; // 多出的宽度
            const extraHeight = this.innerSize.height - this.containerSize.height; //多出的高度
            const left = (-extraWidth / this.containerSize.width) * this.mouseX;
            const top = (-extraHeight / this.containerSize.height) * this.mouseY;
            return {
                transform: `translate(${left}px, ${top}px)`,
            }; 
        }
    }

}
</script>

<style lang="less" scoped>
@import "~@/styles/mixin.less";
    .carousel-item-container{
        // background: gold;
        height: 100%;
        width: 100%;
        position: relative;
        overflow: hidden;
        .carousel-img{
            left: 0;
            top: 0;
            width: 110%;
            height: 110%;
            position: absolute;
            transition: .3s;
        }
        .title,
        .description{
            color: #fff;
            .self-center();
            transform: translateX(0);
            left: 10%;
            letter-spacing: 3px;
            text-shadow: 3px 0 0 rgba(0, 0, 0, 0.5),-1px 0 0 rgba(0, 0, 0, 0.5),
            0 1px 0 rgba(0, 0, 0, 0.5),0 -1px 0 rgba(0, 0, 0, 0.5);
            white-space: nowrap;
            overflow: hidden;
        }
        .title{
            z-index: 3;
            top: 40%;
            font-size: 2em;
            opacity: 0;
        }
        .description{
            z-index: 4;
            font-size: 1em;
            opacity: 0;
        }
        
    }
</style>