<template>
  <div v-show="show" 
       class="to-top-container"
       @click="handleClick">
      Top
  </div>
</template>

<script>
export default {
    data(){
        return{
            show : false,
        }
    },
    created(){
        this.$bus.$on("mainScroll",this.handleScroll);
    },
    beforeDestroy(){
        this.$bus.$off("mainScroll",thi.handleScroll);
    },
    methods : {
        handleScroll(dom){
            if(!dom){
                this.show = false;
                return;
            }
            if(dom.scrollTop > dom.clientHeight){
                this.show = true;
            }
            if(dom.scrollTop <= dom.clientHeight){
                this.show = false;
            }
        },
        handleClick(){
            this.$bus.$emit("setMainScroll", 0);
        }
    }
}
</script>

<style scoped lang="less">
    @import "~@/styles/var.less";
    .to-top-container{
        background: @primary;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        position: fixed;
        z-index: 99;
        right: 50px;
        bottom: 50px;
        cursor: pointer;
        line-height: 50px;
        text-align: center;
        color: #fff;
    }
</style>